:root {
    --bumblebee: #ffff8c;
    --sombra: #3c5369;
}
#form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80vw;
    color: white;
}
.line {
    display: flex;
    justify-content: center;
    margin-bottom: 3%;
}
label {
    text-align: center;
}
textarea {
    width: 20vw;
    height: 10vw;
}
textarea:focus {
    background-color: var(--sombra);
    color: var(--bumblebee);
}
input:focus {
    background-color: var(--sombra);
    color: var(--bumblebee);
}
#message-line {
    margin-left: 8%;
}
#send {
    margin-right: 8%;
    color: black;
}
#send-button {
    background-color: slategrey;
    color: black;
    font-weight: bold;
    font-family: 'Allegreya', sans-serif;
    border: var(--bumblebee) 2px solid;
    border-radius: 5px;
    width: 80px;
    height: 40px;
    margin-left: 100px;
}
#send-button:hover {
    background-color: var(--sombra);
    color: var(--bumblebee);
}