@import url("https://fonts.googleapis.com/css?family=Alegreya+Sans&display=swap");
:root {
  --bumblebee: #e9e9a0;
  --sombra: #3c5369;
}
body {
  font-family: "Alegreya Sans", sans-serif;
}
/*.............
iPhone X view
.............*/

@media screen and (max-width: 812px) {
  .blur {
    -webkit-filter: blur(4px);
    filter: blur(4px);
  }
  .hidden {
    visibility: hidden;
  }
  #header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  #page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
  }
  .certification {
    margin-top: 2.5%;
    margin-bottom: 2.5%;
    height: 80%;
    width: 80%;
  }
  .bumbletext {
    color: var(--bumblebee);
  }
  .home-link {
    color: black;
  }
  .home-link:hover {
    color: var(--bumblebee);
  }
  .thumbnail {
    height: auto;
    max-width: 80vw;
    display: block;
    border: 5px green solid;
  }
  .project-container {
    position: relative;
    text-align: center;
    margin-right: 12.5%;
  }
  .live {
    position: absolute;
    bottom: 42.5%;
    left: 20%;
    border: black solid 1px;
    border-radius: 5px;
    padding: 2px;
    width: 30%;
    height: 15%;
    border-color: white;
    background-color: var(--sombra);
    color: var(--bumblebee);
  }
  .live:active {
    border-color: white;
  }
  .repo {
    position: absolute;
    bottom: 42.5%;
    right: 5%;
    border: solid black 1px;
    border-radius: 5px;
    padding: 2px;
    width: 30%;
    height: 15%;
    border-color: white;
    background-color: var(--sombra);
    color: var(--bumblebee);
  }
  .repo:active {
    border-color: white;
  }
  .script {
    position: absolute;
    bottom: 42.5%;
    left: 40%;
    border: solid black 1px;
    border-radius: 5px;
    padding: 3px;
    width: 30%;
    height: 15%;
    border-color: white;
    background-color: var(--sombra);
    color: var(--bumblebee);
  }
  .script:active {
    border-color: white;
  }
  .project {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  a {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

/*............
Desktop view
..............*/
.blur {
  -webkit-filter: blur(4px);
  filter: blur(4px);
}
.hidden {
  visibility: hidden;
}
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.certification {
  margin-top: 2.5%;
  margin-bottom: 2.5%;
  height: 50%;
  max-width: 100%;
  text-align: center;
}
.bumbletext {
  color: var(--bumblebee);
}
.home-link {
  color: black;
}
.home-link:hover {
  color: var(--bumblebee);
}
.thumbnail {
  height: 300px;
  max-width: 100%;
  display: block;
  margin: 5%;
  border: 5px green solid;
}
.thumbnail:hover {
  filter: blur(4px);
  -webkit-filter: blur(4px);
}
.thumbnail:active {
  filter: blur(4px);
  -webkit-filter: blur(4px);
}
.live {
  position: absolute;
  bottom: 42.5%;
  left: 20%;
  border: black solid 1px;
  border-radius: 5px;
  padding: 2px;
  width: 30%;
  height: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--sombra);
  color: var(--bumblebee);
}
.live:hover {
  border-color: white;
}
.repo {
  position: absolute;
  bottom: 42.5%;
  right: 5%;
  border: solid black 1px;
  border-radius: 5px;
  padding: 2px;
  width: 30%;
  height: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--sombra);
  color: var(--bumblebee);
}
.repo:hover {
  border-color: white;
}
.script {
  position: absolute;
  bottom: 42.5%;
  left: 40%;
  border: solid black 1px;
  border-radius: 5px;
  padding: 3px;
  width: 30%;
  height: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--sombra);
  color: var(--bumblebee);
}
.script:hover {
  border-color: white;
}
.project {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.project-container {
  position: relative;
  text-align: center;
  margin-right: 12.5%;
}
