@import url(https://fonts.googleapis.com/css?family=Alegreya+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Alegreya+Sans&display=swap);
:root {
  --bumblebee: #ffff8c;
  --sombra: #3c5369;
}
body {
  font-family: "Alegreya Sans", sans-serif;
  text-align: center;
  background-color: #3c5369;
  background-color: var(--sombra);
}

#header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#body {
  display: flex;
  width: 50vw;
  justify-content: left;
  align-items: center;
}
#about {
  color: white;
  text-align: left;
  line-height: 200%;
}
#title {
  text-align: center;
  color: #ffff8c;
  color: var(--bumblebee);
}
#subtitle {
  color: #ffff8c;
  color: var(--bumblebee);
  margin-top: 0;
}
#contact {
  display: inline-block;
}
#publications {
  height: 75px;
}
#projects {
  height: 140px;
}
#social-media {
  height: 140px;
}
.nav {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
}
a {
  text-decoration: none;
  color: black;
}

.menu-link {
  background-color: slategrey;
  border-radius: 5px;
  padding: 5px;
  margin-bottom: 1px;
  border: 2px white solid;
}

.non-dropdown {
  margin: 1%;
}

.dropdown {
  position: relative;
  display: inline-block;
  margin: 1%;
  border-radius: 5px;
}

.hide {
  display: none;
  position: absolute;
  background-color: #ffff8c;
  background-color: var(--bumblebee);
  width: 200px;
  border-radius: 5px;
}

.menu-link:hover {
  color: #ffff8c;
  color: var(--bumblebee);
  background-color: #3c5369;
  background-color: var(--sombra);
}
.show {
  display: flex;
  position: absolute;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffff8c;
  background-color: var(--bumblebee);
  width: 200px;
  border-radius: 5px;
}

.show div {
  width: 95%;
  background-color: white;
  text-align: center;
  line-height: 200%;
  margin: 1px;
  color: black;
  border-radius: 3px;
}

.show div:hover {
  background-color: #ffff8c;
  background-color: var(--bumblebee);
}

.show a {
  width: 95%;
  background-color: white;
  text-align: center;
  line-height: 200%;
  margin: 1px;
  color: black;
  border-radius: 3px;
}

.show a:hover {
  background-color: #ffff8c;
  background-color: var(--bumblebee);
}

:root {
  --bumblebee: #e9e9a0;
  --sombra: #3c5369;
}
body {
  font-family: "Alegreya Sans", sans-serif;
}
