@media screen and (max-width: 812px) {
  .blur {
    -webkit-filter: blur(4px);
    filter: blur(4px);
  }
  .hidden {
    visibility: hidden;
  }
  #header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  #page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
  }
  .certification {
    margin-top: 2.5%;
    margin-bottom: 2.5%;
    height: 80%;
    width: 80%;
  }
  .bumbletext {
    color: #e9e9a0;
    color: var(--bumblebee);
  }
  .home-link {
    color: black;
  }
  .home-link:hover {
    color: #e9e9a0;
    color: var(--bumblebee);
  }
  .thumbnail {
    height: auto;
    max-width: 80vw;
    display: block;
    border: 5px green solid;
  }
  .project-container {
    position: relative;
    text-align: center;
    margin-right: 12.5%;
  }
  .live {
    position: absolute;
    bottom: 42.5%;
    left: 20%;
    border: black solid 1px;
    border-radius: 5px;
    padding: 2px;
    width: 30%;
    height: 15%;
    border-color: white;
    background-color: #3c5369;
    background-color: var(--sombra);
    color: #e9e9a0;
    color: var(--bumblebee);
  }
  .live:active {
    border-color: white;
  }
  .repo {
    position: absolute;
    bottom: 42.5%;
    right: 5%;
    border: solid black 1px;
    border-radius: 5px;
    padding: 2px;
    width: 30%;
    height: 15%;
    border-color: white;
    background-color: #3c5369;
    background-color: var(--sombra);
    color: #e9e9a0;
    color: var(--bumblebee);
  }
  .repo:active {
    border-color: white;
  }
  .script {
    position: absolute;
    bottom: 42.5%;
    left: 40%;
    border: solid black 1px;
    border-radius: 5px;
    padding: 3px;
    width: 30%;
    height: 15%;
    border-color: white;
    background-color: #3c5369;
    background-color: var(--sombra);
    color: #e9e9a0;
    color: var(--bumblebee);
  }
  .script:active {
    border-color: white;
  }
  .project {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  a {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

